import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login/login';
import Home from "@/components/home/home";
import revise from "@/components/revise/revise";                            //  
import guide from "@/components/guide/guide";                               //  
import mycontract from "@/components/mycontract/mycontract";                   //  
import myhouse from "@/components/myhouse/myhouse";                         //  
import paypending from "@/components/paypending/paypending";                         //  
import endover from "@/components/endover/endover";                         //  

import repair from "@/components/repair/repair";                            //  
import repairway from "@/components/repair/children/repairway";                   // -
import rentdetail from "@/components/repair/children/rentdetail";                   // 
import needrepair from "@/components/repair/children/needrepair";                   // 
import newRepair from "@/components/repair/children/newRepair";                     //

import register from "@/components/register/register"                         // 
import t404 from "@/components/t404/t404";

// import Test from "@/components/test/test";

Vue.use(Router);


// 
// export default
const routers =  new Router({
  // mode:'history',
  routes: [
    {
      path: '/login',                   // 
      component: Login
    },
    {
      path:'/register',                 // 
      component:register
    },
    {
      path: '/',
      component: Home,
      meta: {
          requireAuth: true,
      },
    }, 
    {
      path: '/revise',                  //      
      component: revise,
      meta: {
          requireAuth: true,
      },
    },
    {
      path: '/guide',                   // 
      component: guide,
      meta: {
          requireAuth: true,
      },
    },
    {
      path: '/mycontract',              //  
      component: mycontract,
      meta: {
          requireAuth: true,
      },
    },
    {
      path: '/myhouse',                 // 
      component: myhouse,
      meta: {
          requireAuth: true,
      },
    },
    {
      path: '/paypending',              // 
      component: paypending,
      meta: {
          requireAuth: true,
      }
    },
    {
      path: '/endover',                 // 
      component: endover,
      meta: {
          requireAuth: true,
      },
    },
    {
    path: '/repair',                    // 
    component: repair,
    meta: {
          requireAuth: true,
    },
    children:[
        {
            path:'list/:status',        // -
            component:repairway,
            meta: {
                requireAuth: true,
            },
        },
        {
            path:'detail/:repairid',          // --
            component:rentdetail,
            meta: {
                requireAuth: true,
            },
        },
        {
            path:'needrepair',          //  
            component:needrepair,
            meta: {
                requireAuth: true,
            },
        },{
            path:'newRepair/:repairid',          // newRepair id
            component:newRepair,
            meta: {
                requireAuth: true,
            },
        }
    ]},{
      path:'/*',
      component:t404
    }
  ]
})


import * as mUtils from "../common/mUtils.js";
import { tokenName } from "../config/env.js";

routers.beforeEach((to, from, next) => {
    if(to.meta.requireAuth){                                      // 
      if(!!mUtils.getCookie(tokenName)){                           // 
        next();
      }else{
        next({
          path:'/login',
          query: {redirect: to.fullPath}                          // path
        })
      }
    }else{
      next()
    }
})
export default routers;
// My contract myhouse  Pending  over


